export const maskCPF = (valor: string) => {
  valor = valor.replace(/\D/g, ""); // Remove caracteres no numricos
  if (valor.length > 11) valor = valor.slice(0, 11); // Limita o tamanho a 11 dgitos
  valor = valor.replace(/(\d{3})(\d)/, "$1.$2"); // Adiciona o ponto
  valor = valor.replace(/(\d{3})(\d)/, "$1.$2"); // Adiciona o segundo ponto
  valor = valor.replace(/(\d{3})(\d{1,2})$/, "$1-$2"); // Adiciona o trao
  return valor;
};

export const maskPhoneNumber = (valor: string) => {
  valor = valor.replace(/\D/g, ""); // Remove caracteres no numricos
  if (valor.length > 11) valor = valor.slice(0, 11); // Limita o tamanho a 11 dgitos

  // Formata o nmero com o cdigo de rea e o nmero
  valor = valor.replace(/^(\d{2})(\d)/g, "($1) $2"); // Adiciona o parntese e espao
  valor = valor.replace(/(\d{5})(\d{1,4})$/, "$1-$2"); // Adiciona o hfen

  return valor;
};
